h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

p:last-child {
  margin-bottom: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  color: currentColor;
  text-decoration: none;
}

button {
  cursor: pointer;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

:root {
  --logo: #ed1d24;
  --background: #0c0b0b;
  --find: #171717;
  --text: #fafafa;
  --text07: rgba(255, 255, 255, .7);
  --text05: rgba(255, 255, 255, .5);
  --text03: rgba(255, 255, 255, .3);
  --border: rgba(250, 250, 250, .15);
  --btn1: #600404;
  --btn2: #5b7f3c;
  --btn3: #34387f;
  --random_characters: #4b50ad;
  --duration: 250ms;
}

.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

body {
  color: #fafafa;
  background-color: var(--background);
  border: 1px solid rgba(250, 250, 250, .3);
  font-family: Poppins;
  font-style: normal;
  font-weight: 400;
}

.container {
  width: 100%;
  padding-left: 15px;
  padding-right: 15px;
}

@media screen and (min-width: 375px) {
  .container {
    width: 365px;
    margin-left: auto;
    margin-right: auto;
  }
}

@media screen and (min-width: 768px) {
  .container {
    width: 734px;
  }
}

@media screen and (min-width: 1440px) {
  .container {
    width: 1406px;
  }
}

header {
  width: 100%;
  z-index: 1000;
  justify-content: center;
  padding: 20px 0;
  position: absolute;
  top: 0;
  left: 0;
}

.header-el {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.header-logo {
  width: 98px;
  height: 15px;
}

.header-color {
  background-color: var(--background);
  box-shadow: 0px 0px 1px var(--text05);
}

.header-icon {
  width: 16px;
  height: 16px;
  fill: var(--text);
  position: absolute;
  top: 50%;
  left: 18px;
  transform: translateY(20%);
}

.header-btn {
  background: none;
  border-color: rgba(0, 0, 0, 0);
  outline: none;
  position: absolute;
}

.header-input {
  color: var(--text);
  border: 1px solid var(--text03);
  width: 108px;
  background: none;
  border-radius: 1000px;
  padding: 7px 18px 7px 42px;
  font-family: Poppins;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
}

.header-input::-ms-input-placeholder {
  color: var(--text);
  font-weight: bold;
}

.header-input::placeholder {
  color: var(--text);
  font-weight: bold;
}

.header-find {
  gap: 40px 16px;
  display: flex;
}

.gallery__link {
  border: none;
  gap: 16px;
  transition: all .3s;
}

.gallery__link:hover, .gallery__link:focus {
  transform: scale(1.05);
}

.gallery, .info-header {
  flex-wrap: wrap;
  display: flex;
}

.gallery {
  justify-content: center;
}

.gallery-item {
  flex-direction: column;
  justify-content: space-between;
  display: flex;
}

.gallery-item__img {
  display: block;
}

.header-find-img {
  width: 332px;
  height: 445px;
  border-radius: 10px;
  margin-bottom: 16px;
}

.header-find-text {
  width: 332px;
  color: var(--text);
  font-size: 18px;
  font-style: normal;
  font-weight: 500;
  line-height: 1.33;
}

@media screen and (min-width: 768px) {
  .header-logo {
    width: 202px;
    height: 31px;
  }

  .header-input {
    width: 130px;
    height: 42px;
    padding: 12px 18px 12px 50px;
    font-size: 14px;
  }

  .header-icon {
    width: 18px;
    height: 18px;
    left: 24px;
    transform: translateY(50%);
  }

  .header-find {
    gap: 16px;
    display: flex;
  }

  .header-find-text {
    font-size: 24px;
    line-height: 1;
  }
}

@media screen and (min-width: 1440px) {
  .container {
    width: 1440px;
    padding-left: 32px;
    padding-right: 32px;
  }

  .header-find {
    gap: 16px;
    display: flex;
  }
}

body {
  background-color: var(--background);
}

.hero {
  margin-top: 92px;
  margin-bottom: 80px;
}

.hero-section {
  color: var(--text);
  font-family: Poppins;
}

.hero-subtitle {
  text-transform: uppercase;
  color: var(--text07);
  margin-bottom: 14px;
  font-family: Poppins;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.29;
}

.hero-title {
  align-items: center;
  margin-bottom: 16px;
  font-size: 44px;
  font-weight: 500;
  line-height: 1;
  display: flex;
}

.hero-desc {
  margin-bottom: 20px;
  font-size: 14px;
  line-height: 1.29;
}

.hero-buttons-list {
  align-items: center;
  gap: 8px;
  display: flex;
}

.hero-btn {
  color: var(--text);
