}

.hero-card-block {
  width: 237px;
  margin-left: auto;
}

.hero-card {
  width: 237px;
  height: 343px;
  background-color: #171717;
  margin-bottom: 14px;
  margin-left: auto;
  padding-top: 120px;
  position: relative;
  overflow: hidden;
}

.stripe {
  height: 1.2px;
  width: 120%;
  background-color: #fff;
  margin-bottom: 13px;
