  background: radial-gradient(81.15% 81.15% at 50% 18.85%, #34387f 0%, #171717 100%);
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
  bottom: 13px;
  left: 17px;
}

.hero-circle-text {
  color: #fafafa;
  text-align: center;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 1.1;
  list-style: none;
}

.hero-circle-text li:nth-child(2), .hero-circle-text li:nth-child(3) {
