}

.characters-border-line {
  width: 237px;
  margin-bottom: 16px;
  margin-left: auto;
  position: relative;
}

.characters {
  color: var(--text);
  font-size: 14px;
  font-weight: 400;
  line-height: 1.17;
}

.characters-section {
  justify-content: flex-end;
  gap: 30px;
  display: flex;
}

.characters-desc {
  width: 127px;
  color: var(--text05);
  font-size: 12px;
  font-weight: 400;
  line-height: 1.17;
}

.slider-img, .img-card {
  width: 237px;
  height: 343px;
}



